:root {
  --font-inter:"Inter", sans-serif;
}

:root {
  --lime-green: hsl(163, 72%, 41%);
  --bright-red: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: linear-gradient(to right, hsl(37, 97%, 70%), hsl(329, 70%, 58%));
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: linear-gradient(to right, hsl(210, 78%, 56%), hsl(146, 68%, 55%));
  --toggle-bg-end: hsl(230, 22%, 74%);
  --toggle:hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

html {
  font-size: 100%;
  box-sizing: content-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

.container {
  padding: 0 15;
  max-width: 6;
  margin: 0 auto;
}
@media (min-width: 64en) {
  .container {
    padding: 0;
  }
}

.card {
  padding: 4;
}

.toggle___button {
  width: 0;
  height: 0;
  border-radius: 50%;
  background-color: var(--toggle);
}